import * as React from 'react';
import {Logo, LogoSize} from '@here/hds-logo';

// eslint-disable-next-line no-new
new Logo();

interface HDSLogoProperties extends React.HTMLAttributes<HTMLElement> {
  inverted?: boolean;
  monochrome?: boolean;
  size?: LogoSize;
  padding?: boolean;
  background?: boolean;
}

const HDSLogo = React.forwardRef((props: HDSLogoProperties, ref) => {
  const {className, monochrome, size, padding, inverted, background, ...rest} = props;
  return (
    <hds-logo
      ref={ref}
      size={size}
      {...(className && {class: className})}
      {...(inverted && {inverted})}
      {...(background && {background})}
      {...(monochrome && {monochrome})}
      {...(padding && {padding})}
      {...rest}
    />
  );
});

HDSLogo.defaultProps = {
  size: 'extra-small',
  inverted: false,
  background: false,
  monochrome: false,
  padding: false,
};

export default HDSLogo;
